@import 'variables';

.modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    z-index: 1;
    overflow: auto;

    p {
        padding-top: 7px;
        padding-bottom: 7px;
    }
    ul {
        list-style: inside;
    }
    &.hidden {
        display: none;
    }

    .modal-dialog {
        margin: 15% auto; /* 15% from the top and centered */
        // padding: 20px;
        border: 1px solid #888;
        background-color: white;
        width: 80%; /* Could be more or less, depending on screen size */

        .modal-header {
            font-size: larger;
            background-color: $header-color;
            width: 100%;

            .close-button {
                float: right;
                padding: 1px;
            }
            .modal-title {
                color: white;
                text-align: center;
                margin-left: auto;
                width: 100%;
            }
            .modal-body {
                padding: 40px;
            }
        }
    }
}

.checklist-editor {

    hr {
        margin-top: 5px;
        margin-bottom: 5px;
    }

    b {
        font-weight: bold;
    }

    a {
        color: purple;
    }

    colgroup {
        width: 100%;
    }

    fieldset {
        padding-top: 5px;
        padding-bottom: 5px;
        margin-left: 45px;
    }
    fieldset.top-level {
        margin-left: 5px;
    }

    legend {
        padding-left: 5px;
    }

    div.changed-ids {
        margin-top: 20px;
    }

    table {
        width: 100%;
        margin-top: 5px;
        margin-bottom: 5px;
    }
    th {
        text-align: left;
        font-weight: bold;
    }
    label {
        padding-left: 5px;
    }

    button {
        background-color: white;
    }
    /*button:disabled {
        background-color: lightgray;
        pointer-events: none;
    }*/
    button.swap-btn {
        font-size: smaller;
        padding-top: 2px;
        padding-bottom: 2px;
        padding-left: 3px;
        padding-right: 3px;
        border-radius: 0;
    }
    button.delete-btn {
        margin-left: 10px;
        background-color: red;
        color: white;
        border: 1px solid red;
        font-size: smaller;
        padding-top: 1px;
        padding-bottom: 1px;
        padding-left: 3px;
        padding-right: 3px;
        border-radius: 0;
    }
    button.add-btn {
        margin-left: 10px;
        // font-size: smaller;
        color: white;
        background-color: green;
        padding-top: 0;
        padding-bottom: 0;
        padding-left: 3px;
        padding-right: 3px;
        border: 1px solid green;
        border-radius: 0;
    }
    button.save-btn {
        background-color: red;
        color: white;
        flex: 1;
        justify-content: flex-end;
        // width: 60px;
        flex-grow: 0;
    }
    .breadcrumbs {
        border: 1px solid black;
        padding: 2px;
        margin-bottom: 20px;
    }

    .has-error {
        border: 1 px solid red;
        background-color: pink;
    }

    .error-message {
        color: red;
    }
    .phase {
        border: 1px solid black;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-top: 2px;
        margin-bottom: 2px;
    }

    .sub-phase {
        padding-top: 10px;
        padding-bottom: 10px;
        margin-top: 2px;
        margin-bottom: 2px;
    }

    .assessment-with-options {
        border: 1px solid red;
    }
    .decision {
        border: 1px solid blue;
    }

    .add-item {
        margin-top: 10px;
        margin-bottom: 4px;
    }

    .indent-1 {
        margin-left: 20px;
    }
    .indent-2 {
        margin-left: 40px
    }
    .indent-3 {
        margin-left: 60px;
    }
    .indent-4 {
        margin-left: 80px;
    }

    // .save-button-wrapper {
    //     display: flex;
    //     flex-flow: row-reverse;
    // }

}



.scenario-type-selector {
    padding: 1.5rem;

    a {
        display: inline-block;
        padding: 2rem 3rem;
        border: 1px solid #333;
        border-radius: 0.5rem;
        font-size: 1.25rem;
        margin: 0.5rem 0;

        &:hover {
            background-color: #ddd;
        }
    }
}

.hidden {
    display: none;
}
