@import 'variables';

.tab-notebook {
    background-color: $header-dark;
    width: 100%;
    height: 100%;

    .tab-list {
        width: 100%;
        height: 50px;

        .tab-button {
            position: relative;
            background-color: darkgray;
            color: white;

            .change-indicator {
                position: absolute;
                top: -15px;
                right: 5px;
                font-size: xx-large;
                color: white;
            }
            &.active {
                background-color: royalblue;
            }
        }

    }
    .tabs-content {
        display: block;
        width: 100%;
        height: calc(100% - 50px);
        background-color: white;

        .tab-pane {
            width: 100%;
            height: 100%;
            padding: 10px;
        }

    }
}