@import 'variables';

.modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    z-index: 1;
    overflow: auto;

    p {
        padding-top: 7px;
        padding-bottom: 7px;
    }
    ul {
        list-style: inside;
    }
    &.hidden {
        display: none;
    }

    .modal-dialog {
        margin: 15% auto; /* 15% from the top and centered */
        // padding: 20px;
        border: 1px solid #888;
        background-color: white;
        width: 80%; /* Could be more or less, depending on screen size */

        .modal-header {
            font-size: larger;
            background-color: $header-color;
            width: 100%;

            .close-button {
                float: right;
                padding: 1px;
            }
            .modal-title {
                color: white;
                text-align: center;
                margin-left: auto;
                width: 100%;
            }
            .modal-body {
                padding: 40px;
            }
        }
    }
}

.scenario-editor {

    fieldset {
        margin: 5px;
        margin-bottom: 15px;
        padding: 5px;
    }

    legend {
        padding-left: 5px;
        font-weight: bold;
        font-size: x-large;
    }

    button {
        background-color: white;
    }

    // button:disabled {
    //     background-color: lightgray;
    //     pointer-events: none;
    // }

    button.swap-btn {
        font-size: smaller;
        padding-top: 2px;
        padding-bottom: 2px;
        padding-left: 3px;
        padding-right: 3px;
        border-radius: 0;
    }

    button.delete-btn {
        // margin-left: 10px;
        background-color: red;
        color: white;
        border: 1px solid red;
        font-size: smaller;
        padding-top: 1px;
        padding-bottom: 1px;
        padding-left: 3px;
        padding-right: 3px;
        border-radius: 0;
    }

    button.add-btn {
        margin-left: 10px;
        color: white;
        background-color: green;
        padding-top: 0;
        padding-bottom: 0;
        padding-left: 3px;
        padding-right: 3px;
        border: 1px solid green;
        border-radius: 0;
    }

    button.save-btn {
        background-color: red;
        color: white;
        flex: 1;
        justify-content: flex-end;
        // width: 60px;
        flex-grow: 0;
    }

    .breadcrumbs {
        border: 1px solid black;
        padding: 2px;
        margin-bottom: 20px;
    }

    .details-editor {
        table {
            border: 0;
        }
    }

    .inline-editor {
        padding-top: 20px;
        // padding-bottom: 20px;
        table {
            border-bottom: 0;
        }

        thead {
            background-color: lightgray;
        }

        th {
            border-bottom: 1px solid black;
        }
    }

    .add-entity {
        padding: 20px;

        table {
            border: 0;
        }

        thead {
            background-color: white;
        }

        th {
            border-bottom: 0;
        }

        td {
            vertical-align: top;
        }
    }

    .err-msg {
        color: red;
    }

    #tab-selector {
        display: flex;
        flex-flow: row wrap;
        background-color: $header-color;

        button {
            all: unset;
        }

        .active-tab {
            background-color: $header-dark;
        }

        .tab-button {
            color: #ddd;
            cursor: pointer;
            padding: 0.75em 1.25em;

            &:hover {
                background-color: $header-dark;
            }
        }
    }


}

.scenario-form-wrapper {
    border-top: 1px solid #222;
    width: 100%;
    height: 100% -58px - 48px; //subtract header and tab heights
    overflow-y: scroll;

    .scenario-form {
        padding: 0.5rem;

        &.scenario-view-hidden {
                    display: none !important;
            }

        .save-changes {
            float: right;
        }

        .info-wrapper {
            h1 {
                color: $pastel-blue;
            }
        }

        #basic-info {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            h1 {
                color: $pastel-blue;
            }

            label {
                display: flex;
                flex-direction: column;
                font-weight: 500;
            }
        }

        #sample,
        #opqrst {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            label {
                display: flex;

                input {
                    display: inline-block;
                    margin: 0 1rem;
                    width: 100%;
                }
            }
        }

        #medications {
            // display: flex;
            // flex-direction: column;
            // gap: 0.5rem;

            .meds-wrapper {
                // display: grid;
                // grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
                // gap: 0.5rem;
                table {
                    width: 100%;
                    th {
                        font-weight: bold;
                    }
                    thead {
                        th {
                            text-align: center;
                        }
                    }
                    tbody {
                        tr {
                            outline: 1px solid #333;
                        }

                        td {
                            padding: 0.35rem;
                        }

                        input[type=text] {
                            width: 100%;
                        }

                        .value-hint {
                            font-size: 0.9rem;
                            color: #bbb;
                        }

                        .route-dropdown {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }

                        .contraindications {
                            position: relative;
                            user-select: none;
                            margin: auto;

                            .hidden { display: none !important; }

                            .dropdown {
                                background: #eee;
                                border-radius: 0.5rem;
                                padding: 0.25rem 0.5rem;
                                cursor: pointer;
                                width: min-content;
                                white-space: nowrap;

                                box-shadow: 2px 3px 3px rgba(0, 0, 0, 0.5);
                            }

                            .checklist {
                                position: absolute;
                                background-color: #fff;
                                border: 1px solid #999;
                                border-radius: 0.5rem;
                                max-height: 15rem;
                                overflow-y: auto;
                                z-index: 99;
                                text-align: left;

                                .contraindication {
                                    white-space: nowrap;
                                    width: 100%;

                                    label {
                                        display: block;
                                        width: 100%;
                                    }

                                    input[type=checkbox] {
                                        vertical-align: middle;
                                        margin-right: 0.5rem;
                                    }
                                }
                            }
                        }
                    }
                }

                label {
                    border: 1px solid #ddd;
                    border-style: none none solid none;
                    padding: 0.5em;

                    input {
                        max-width: 15em;
                        margin-left: auto;
                        margin-right: 3em;
                    }
                }
            }
        }

        .vitals {
            // display: grid;
            // grid-template-columns: repeat(auto-fill, minmax(30rem, 1fr));
            // gap: 1rem;
            table {
                width: 100%;
                 th {
                        font-weight: bold;
                    }
                thead {
                    th {
                        text-align: center;
                    }
                }
                tbody {
                    input {
                        width: 100%;
                    }
                }
            }
            .vital-wrapper {
                border: 1px solid $header-color;
                border-radius: 0.5rem;
                padding: 0.5rem;
                max-width: calc(100vw - 1rem);

                h1 {
                    color: $pastel-blue;
                }

                .vitals-box {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    gap: 1rem;

                    label {
                        display: flex;
                        font-weight: 500;

                        input {
                            display: inline-block;
                            margin: 0 0 0 1rem;
                            width: 100%;
                        }
                    }
                }
            }
        }

        #scenario-steps {
            display: flex;
            flex-direction: column;

            h1 {
                color: $pastel-blue;
            }

            .header-item {
                h2 {
                    color: $pastel-blue;
                }
            }

            .step-item {
                border: 1px solid #333;
                border-style: none none solid none;
                padding: 0.5rem;

                &:nth-child(odd) {
                    background-color: #eee;
                }

                label {
                    display: flex;
                    align-items: center;
                    white-space: nowrap;

                    input {
                        display: inline-block;
                        max-height: min-content;
                        align-self: flex-end;
                        margin-left: 1em;
                        width: 100%;
                    }
                }
            }
        }
    }
       #submit-next {
            background-color: $pastel-blue;
            padding: 0.75em 1.25em;
            color: #ddd;
            margin-left: auto;
            cursor: pointer;
        }
}


.hidden {
    display: none;
}

.error {
    color: red;
}
