@import 'variables';

.medications-form {
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    .medication {
        .main-area {
            display: flex;
            gap: 0.5rem;
            align-items: center;
        }

        h3 {
            color: $pastel-blue;
            text-align: center;
            min-width: 10rem;
            width: 13rem;
        }

        .expanded-area {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;
            $columns: 1fr 2fr 2fr 2fr 2fr 2fr 2fr 2fr;

            .generic-data {
                display: flex;
                gap: 1rem;
                margin-top: 1rem;

                .contraindications, .indications {
                    position: relative;
                    user-select: none;

                    .hidden { display: none !important; }

                    .dropdown {
                        background: #eee;
                        border-radius: 0.5rem;
                        padding: 0.25rem 0.5rem;
                        cursor: pointer;
                        width: min-content;
                        white-space: nowrap;

                        box-shadow: 2px 3px 3px rgba(0, 0, 0, 0.5);
                    }

                    .checklist {
                        position: absolute;
                        background-color: #fff;
                        border: 1px solid #999;
                        border-radius: 0.5rem;
                        max-height: 15rem;
                        overflow-y: auto;
                        z-index: 99;

                        .contraindication, .indication {
                            padding: 0.2rem;
                            white-space: nowrap;
                            
                            label {
                                display: flex;
                                gap: 0.5rem;
                                align-items: center;
                                padding: 0 0.5rem;

                                input[type=checkbox] {
                                    margin-top: 0.5em;
                                }
                            }
                        }
                    }
                }
            }

            .headings {
                display: grid;
                grid-template-columns: $columns;
                gap: 1rem;
                align-items: center;
                justify-items: center;
                padding: 0.25rem 0;
                
                p {
                    font-weight: bold;
                    text-align: center;
                }
            }
            
            .adult-data, .pediatric-data {
                display: flex;
                flex-direction: column;
                background: #f5f5f5;
                padding: 0.5rem;
                gap: 0.5rem;
                
                .basic-info {
                    display: grid;
                    grid-template-columns: $columns;
                    gap: 0.5rem;
                    align-items: center;

                    input[type=text],
                    input[type=number] {
                        width: 100%;
                    }

                    input[type=checkbox] {
                        margin: 0 auto;
                    }

                    p {
                        font-weight: bold;
                        text-decoration: underline;
                    }
                }
            }

            .administration-routes {
                display: flex;
                gap: 1rem;
                align-items: center;

                p {
                    font-weight: bold;
                }
    
                label {
                    display: flex;
                    gap: 0.25rem;
                    align-items: center;
                }
            }
        }
    }

    .expand-bttn {
        border: none;
        display: flex;
        align-items: center;

        &:hover {
            background-color: #ddd;
        }

        svg {
            height: 1rem;
            width: 1rem;
        }
    }

    .expand-bttn.expanded {
        background-color: #ddd;

        &:hover {
            background-color: #999;
        }
    }

    .expanded-area.collapsed {
        display: none;
    }

    .delete-bttn {
        all: unset;
        background-color: #ddd;
        padding: 0 1rem;
        max-width: min-content;
        cursor: pointer;

        &:hover {
            background-color: aquamarine;
        }
    }

    button {
        margin: 0;
        align-self: flex-start;
    }
}