@import 'variables';

.scenario-type-editor {

    b {
        font-weight: bold;
    }

    a {
        color: purple;
    }

    table {
        width: 100%;
        border: 1px solid black;
        border-spacing: 0;
        margin-top: 5px;
        margin-bottom: 5px;
    }
    colgroup {
        width: 100%;
    }
    th {
        text-align: left;
        font-weight: bold;
    }

    fieldset {
        margin: 5px;
        margin-bottom: 15px;
        padding: 5px;
    }
    legend {
        padding-left: 5px;
        font-weight: bold;
    }
    button {
        background-color: white;
    }

    // button:disabled {
    //     background-color: lightgray;
    //     pointer-events: none;
    // }

    button.swap-btn {
        font-size: smaller;
        padding-top: 2px;
        padding-bottom: 2px;
        padding-left: 3px;
        padding-right: 3px;
        border-radius: 0;
    }

    button.delete-btn {
        // margin-left: 10px;
        background-color: red;
        color: white;
        border: 1px solid red;
        font-size: smaller;
        padding-top: 1px;
        padding-bottom: 1px;
        padding-left: 3px;
        padding-right: 3px;
        border-radius: 0;
    }

    button.add-btn {
        margin-left: 10px;
        color: white;
        background-color: green;
        padding-top: 0;
        padding-bottom: 0;
        padding-left: 3px;
        padding-right: 3px;
        border: 1px solid green;
        border-radius: 0;
    }

    button.save-btn {
        background-color: red;
        color: white;
        flex: 1;
        justify-content: flex-end;
        // width: 60px;
        flex-grow: 0;
    }

    .breadcrumbs {
        border: 1px solid black;
        padding: 2px;
        margin-bottom: 20px;
    }

    .details-editor {
        table {
            border: 0;
        }
    }
    .inline-editor {
        table {
            border-bottom: 0;
        }
        thead {
            background-color: lightgray;
        }
        th {
            border-bottom: 1px solid black;
        }
    }
    .add-entity {
        table {
            border: 0;
        }
        thead {
            background-color: white;
        }
        th {
            border-bottom: 0;
        }
        td {
            vertical-align: top;
        }
    }

    .err-msg {
        color: red;
    }

    .save-button-wrapper {
        display: flex;
        flex-flow: row-reverse;
    }

    #tab-selector {
        display: flex;
        flex-flow: row wrap;
        background-color: $header-color;

        button {
            all: unset;
        }

        .active-tab {
            background-color: $header-dark;
        }

        .tab-button {
            color: #ddd;
            cursor: pointer;
            padding: 0.75em 1.25em;

            &:hover {
                background-color: $header-dark;
            }
        }
    }

}
